/* stylelint-disable */

/*! normalize.css v8.0.1 | MIT License | github.com/necolas/normalize.css */

/* Document
   ========================================================================== */

/**
 * 1. Correct the line height in all browsers.
 * 2. Prevent adjustments of font size after orientation changes in iOS.
 */

$bred: 0.5px solid red;

html {
  line-height: 1.15; /* 1 */
  -webkit-text-size-adjust: 100%; /* 2 */
}

/* Sections
   ========================================================================== */

/**
 * Remove the margin in all browsers.
 */



//_______________________header.html

.page-header {
  display: flex;
  width: 1207px;
  height: 91px;
  margin: 0 auto;
  border: $bred;
}

.page-header__logo {
  display: flex;
  width: 207px;
  justify-content: flex-start;
  border: $bred;
  padding-top: 27px;
}

.btn-page-header__login {
  display: flex;
  width: 207px;
  justify-content: flex-start;
  border: $bred;
  padding-top: 34px;
}

.main-nav {
  display: flex;
  justify-content: flex-start;
  margin: 0;
  padding: 40px 0 0 0;
  width: 797px;
  font-family: Roboto;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 19px;
  color: #1F1E25;
  border: $bred;
}

.main-nav li{
  padding-right: 25px;
}

//_______________________promo.html

.promo {
  width: 1366px;
  height: 400px;
  background-image: url("/img/promo.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  border: $bred;
}

//_______________________offers-menu.html

.page-offers-menu { // высота должна быть 673
  display: block;
  height: 503px;
  border: $bred;
  padding-top: 60px;
  padding-bottom: 110px;

  &__inner {
    display: flex;
    height: 503px;
    border: $bred;
  }
}

//_______________________map.html

.page-map {
  height: 643px;
  border: $bred;
  padding-bottom: 110px;
}

.page-map__inner {
  display: flex;
  flex-direction: column;
}

.page-map__title {
  font-family: Roboto;
  font-style: normal;
  font-weight: bold;
  font-size: 41px;
  line-height: 140%;
  height: 99px;
  border: $bred;
}

.page-map__contries {
  height: 82px;
  border: $bred;
  background: #F6F7FF;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: Roboto;
  font-style: normal;
  font-weight: normal;
  font-size: 14px;
  line-height: 140%;

  div {
    margin-right: 30px;
    border: $bred;
  }
}

//_______________________footer.html

.page-footer { // высота должна быть 224px
  height: 175px;
  background: #F6F7FF;
  border: $bred;
  padding-top: 49px;
}

.page-footer__inner {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
}

.page-footer__logo {
  border: $bred;
  margin-right: 81px;
}

.page-footer__menu {
  margin-top: 11px;
  font-family: Roboto;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 140%;
  margin-right: 80px;
  border: $bred;
  ul {
    display: flex;
    width: 144px;
    height: 104px;
    flex-direction: column;
    justify-content: space-between;
  }
}

.page-footer__contacts {
  margin-top: 11px;
  border: $bred;
}

.page-footer__socials {
  margin-top: 11px;
  border: $bred;
  margin-left: auto;
}





